import {getLocaleValue} from "./I18nUtils";

export function getSmartDateStr(paramDateStr) {
    try {
        let paramDateObj = parseDate(paramDateStr);
        let nowDate = new Date();
        nowDate = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), 0, 0, 0);
        let nowYear = nowDate.getFullYear();
        let paramDateObjNext = new Date(paramDateObj.getTime() + (24 * 60 * 60 * 1000));
        if (paramDateObj.getTime() === nowDate.getTime()) {
            return getLocaleValue("date_t");
        }
        if (paramDateObjNext.getTime() === nowDate.getTime()) {
            return getLocaleValue("date_y");
        }
        if (paramDateObj.getFullYear() === nowYear) {
            return (paramDateObj.getMonth() + 1) + "月" + paramDateObj.getDate() + "日";
        }
        return (paramDateObj.getFullYear()) + "年" + (paramDateObj.getMonth() + 1) + "月" + paramDateObj.getDate() + "日";
    } catch (e) {
        return "未知";
    }
}

export function parseDate(dateStr) {
    let dateItemArray = dateStr.split("-");
    if (dateItemArray.length !== 3) {
        dateItemArray = dateStr.split("/");
    }
    if (dateItemArray.length !== 3) {
        return 0;
    }
    let year = parseInt(dateItemArray[0]);
    let month = parseInt(dateItemArray[1]);
    let day = parseInt(dateItemArray[2]);
    let dateObj = new Date(year, (month - 1), day, 0, 0, 0);
    return dateObj;
}

