import {Prism as SyntaxHighlighter} from "react-syntax-highlighter";
import {oneDark} from "react-syntax-highlighter/dist/cjs/styles/prism";
import ReactMarkdown from "react-markdown";
import {isNotUndefined} from "../js/StringUtils";
import {useEffect, useState} from "react";
import SysLoading from "./SysLoading";

function SysMarkdown(props) {
    let [mdContent, setMdContent] = useState("");
    useEffect(() => {
        if (isNotUndefined(props.mdContent)) {
            setMdContent(props.mdContent);
        }
        if (isNotUndefined(props.url)) {
            fetch(props.url)
                .then((res) => res.text())
                .then((text) => setMdContent(text));
        }
    });
    return (
        <div>
            <SysLoading show={mdContent === "" && isNotUndefined(props.url)}></SysLoading>
            <ReactMarkdown
                components={{
                    img(props) {
                        return (
                            <div style={{textAlign: 'center', margin: "0 auto"}}>
                                <img {...props} style={{maxWidth: '80%'}} alt=""/>
                            </div>
                        )
                    },
                    code({node, inline, className, children, ...props}) {
                        const match = /language-(\w+)/.exec(className || '')
                        return !inline && match ? (
                            <SyntaxHighlighter
                                children={String(children).replace(/\n$/, '')}
                                language={match[1]}
                                style={oneDark}
                                PreTag="div"
                                {...props}
                            />
                        ) : (
                            <code className={className} {...props}>
                                {children}
                            </code>
                        )
                    }
                }}
            >{mdContent}</ReactMarkdown>
        </div>
    )
}

export default SysMarkdown;