import {handleUndefined} from "../js/StringUtils";
import SysIcon from "./SysIcon";

function SysLoading(props) {
    let show = handleUndefined(props.show, false);
    return (
        <div className="sys-loading" style={{display: show ? "display" : "none"}}>
            <div className="sys-loading-icon">
                <SysIcon iconName="icon-jiazai"/>
            </div>
            <div style={{fontSize: "20px"}}>正在加载...</div>
        </div>
    );
}

export default SysLoading;