import {getSmartDateStr, parseDate} from "./TimeUtils";
import {sortJson} from "./Sort";
import * as MathUtils from "./MathUtils";
import {isUndefined} from "./StringUtils";

const textImagColor = {
    "java": "#3a75b0",
    "react": "#61dafb"
};

function getLocalMdData(type) {
    let mdBlogJson = [
        {
            "type": "react-blog",
            "textImgName": "React 教程",
            "textImgColor": textImagColor["react"],
            "img": "img/helloReact.png",
            "timeValue": parseDate("2023-04-01").getTime(),
            "timeStr": getSmartDateStr("2023-04-01"),
            "title": "React 入门",
            "subTitle": "React项目构建，组件，路由使用",
            "url": "data/hello-react.md"
        }
    ];
    let mdQJson = [
        {
            "type": "java-q",
            "textImgName": "Java 问答",
            "textImgColor": textImagColor["java"],
            "img": "img/javaQ.png",
            "timeValue": parseDate("2023-05-14").getTime(),
            "timeStr": getSmartDateStr("2023-05-14"),
            "title": "Java 面向对象",
            "subTitle": "封装，继承，多态",
            "url": "data/java-obj.md"
        }, {
            "type": "java-q",
            "textImgName": "Java 问答",
            "textImgColor": textImagColor["java"],
            "img": "img/javaQ.png",
            "timeValue": parseDate("2023-05-14").getTime(),
            "timeStr": getSmartDateStr("2023-05-14"),
            "title": "Java 日期",
            "subTitle": "格式化，解析，工具类",
            "url": "data/java-date.md"
        }, {
            "type": "java-q",
            "textImgName": "Java 问答",
            "textImgColor": textImagColor["java"],
            "img": "img/javaQ.png",
            "timeValue": parseDate("2023-06-10").getTime(),
            "timeStr": getSmartDateStr("2023-06-10"),
            "title": "Java IO流",
            "subTitle": "字节流，字符流，对象流",
            "url": "data/java-io.md"
        }, {
            "type": "java-q",
            "textImgName": "Java 问答",
            "textImgColor": textImagColor["java"],
            "img": "img/javaQ.png",
            "timeValue": parseDate("2023-06-10").getTime(),
            "timeStr": getSmartDateStr("2023-06-10"),
            "title": "Java 数组",
            "subTitle": "List，Set，Map",
            "url": "data/java-array.md"
        }, {
            "type": "java-q",
            "textImgName": "Java 问答",
            "textImgColor": textImagColor["java"],
            "img": "img/javaQ.png",
            "timeValue": parseDate("2023-06-10").getTime(),
            "timeStr": getSmartDateStr("2023-06-10"),
            "title": "Java 多线程",
            "subTitle": "线程，线程池，JUC",
            "url": "data/java-io.md"
        }
    ];
    let mdJson = [];
    if (type === "blog") {
        for (let index in mdBlogJson) {
            mdJson.push(mdBlogJson[index]);
        }
    } else if (type === "q") {
        for (let index in mdQJson) {
            mdJson.push(mdQJson[index]);
        }
    } else {
        for (let index in mdBlogJson) {
            mdJson.push(mdBlogJson[index]);
        }
        for (let index in mdQJson) {
            mdJson.push(mdQJson[index]);
        }
    }
    sortJson(mdJson, "timeValue");
    return mdJson;
}

export function getMdData(type) {
    if (isUndefined(type)) type = "all";
    return getLocalMdData(type);
}

export function getNextBlog(type) {
    let blogList = getMdData("all");
    let blogSize = blogList.length;
    let nextBlog;
    if (blogSize === 1) {
        nextBlog = blogList[0];
    } else {
        let nextBlogNum = MathUtils.randomNum(blogList.length);
        nextBlog = blogList[nextBlogNum - 1];
    }
    return nextBlog;
}


export function getSearchDataList(key) {
    let mdData = getMdData("all");
    let searchDataList = [];
    if (key) {
        key = key.toLocaleLowerCase();
        mdData.forEach((item, index) => {
            let title = item.title.toLocaleLowerCase();
            let subTitle = item.subTitle.toLocaleLowerCase();
            let type = item.type.toLocaleLowerCase();
            let timeStr = item.timeStr.toLocaleLowerCase();
            if (title.indexOf(key) !== -1 || subTitle.indexOf(key) !== -1 || type.indexOf(key) !== -1 || timeStr.indexOf(key) !== -1) {
                searchDataList.push(item);
            }
        });
    }
    return searchDataList;
}

