import {handleUndefined} from "../js/StringUtils";
import SysIcon from "./SysIcon";

function SysPopup(prop) {
    let show = handleUndefined(prop.show, false);
    let setShow = handleUndefined(prop.setShow, undefined);
    return (
        <div style={{display: show ? "block" : "none"}} onClick={(e) => {
            e.stopPropagation();
        }}>
            <div className="sys-popup animate__animated animate__slideInUp">
                <div className="sys-popup-content">
                    <div style={{paddingBottom: "10vh"}}>
                        {prop.content}
                    </div>
                </div>
                <div className="sys-popup-close-btn" onClick={() => {
                    setShow(false);
                }}>
                    <SysIcon iconName="icon-close"/>
                </div>
            </div>
        </div>
    );
}

export default SysPopup;