import {handleUndefined} from "../js/StringUtils";
import React, {useEffect, useState} from "react";

function SysHeader(props) {
    let left = handleUndefined(props.option.left, "");
    let leftClick = handleUndefined(props.option.leftClick, undefined);
    let mid = handleUndefined(props.option.mid, "");
    let right = handleUndefined(props.option.right, "");
    let rightClick = handleUndefined(props.option.rightClick, undefined);
    let show = handleUndefined(props.show, true);
    let headerAutoActive = handleUndefined(props.option.headerAutoActive, false);
    let headerAutoActiveHeight = handleUndefined(props.option.headerAutoActiveHeight, 0);
    const [headerActive, setHeaderActive] = useState(false);

    useEffect(() => {
        if (headerAutoActive) {
            document.getElementsByClassName("main")[0].addEventListener("scroll", function (e) {
                const upValue = e.target.scrollTop;
                if (upValue > headerAutoActiveHeight) {
                    setHeaderActive(true);
                } else {
                    setHeaderActive(false);
                }
            });
        }
    });
    return (
        <div style={{display: show ? "block" : "none"}}>
            <div className={[headerActive ? "header active" : "header"]}>
                <div className="header-nav-left" onClick={() => leftClick()}>{left}</div>
                <div className="header-nav-mid">{headerActive ? mid : ""}</div>
                <div className="header-nav-right" onClick={() => rightClick()}>{right}</div>
            </div>
        </div>
    )
}

export default SysHeader;