const enJson = {
    "date_y": "Yesterday",
    "date_t": "Today"
};
const zhJson = {
    "date_y": "昨天",
    "date_t": "今天"
};

export function getLocaleValue(localeKey) {
    let language = localStorage.getItem("language");
    switch (language) {
        case "en" :
            return enJson[localeKey];
        case "zh" :
            return zhJson[localeKey];
        default:
            return zhJson[localeKey];
    }
}