import "../../css/blog/blog.css";
import SysLoading from "../SysLoading";
import {useEffect, useState} from "react";
import SysMarkdown from "../SysMarkdown";


function BlogMarkDown(props) {
    let [mdContent, setMdContent] = useState("");
    useEffect(() => {
        fetch(props.url)
            .then((res) => res.text())
            .then((text) => setMdContent(text));
    });
    return (
        <div>
            <SysLoading show={mdContent === ""}/>
            <div style={{display: mdContent !== "" ? "" : "none"}}>
                <div className="blog-detail-text-img" style={{backgroundColor: '#' + props.textImgColor}}>
                    {props.textImgName}
                </div>
                <div className="blog-detail" style={{paddingBottom: props.nextFlag ? "70px" : "0px"}}>
                    <div className="blog-detail-title">{props.title}</div>
                    <hr/>
                    <SysMarkdown mdContent={mdContent}></SysMarkdown>
                </div>
            </div>
        </div>
    );
}

export default BlogMarkDown;