import BlogMarkDown from "../../component/Blog/BlogMarkDown";
import SysHeader from "../../component/SysHeader";
import SysMain from "../../component/SysMain";
import {useNavigate} from "react-router";
import {useSearchParams} from "react-router-dom";
import React from "react";
import SysIcon from "../../component/SysIcon";
import {handleUndefined} from "../../js/StringUtils";
import {getNextBlog} from "../../js/DataUtils";

function BlogDetail() {
    const navigate = useNavigate();
    const [searchParams] = useSearchParams();
    const url = searchParams.get("url");
    const title = searchParams.get("title");
    const textImgColor = searchParams.get("textImgColor");
    const textImgName = searchParams.get("textImgName");
    const nextFlag = handleUndefined(searchParams.get("nextFlag"), false);
    const headerOption = {
        left: nextFlag ? <SysIcon iconName="icon-home"/> : <SysIcon iconName="icon-back"/>,
        leftClick: () => {
            navigate("/");
        },
        mid: title,
        right: "",
        headerAutoActive: true,
        headerAutoActiveHeight: 300
    };
    return (
        <div>
            <SysHeader option={headerOption}></SysHeader>
            <SysMain>
                <BlogMarkDown url={url} title={title} textImgName={textImgName}
                              textImgColor={textImgColor} nextFlag={nextFlag}></BlogMarkDown>
                <div className="blog-detail-next-btn" style={{display: nextFlag ? "fixed" : "none"}} onClick={() => {
                    let nextBlog = getNextBlog();
                    navigate("/blog/detail/?url=" + nextBlog.url + "&title=" + nextBlog.title + "&textImgColor="
                        + nextBlog.textImgColor.replace('#', '') + "&textImgName=" + nextBlog.textImgName + "&nextFlag=ture", {});
                }}>
                    <SysIcon iconName="icon-down"/>
                </div>
            </SysMain>
        </div>
    );
}

export default BlogDetail;