import React from "react";
import {handleUndefined} from "../js/StringUtils";

function SysIcon(props) {
    return (
        <div className={handleUndefined(props.className, undefined)}>
            <span style={{
                color: handleUndefined(props.color, undefined),
                fontSize: handleUndefined(props.size, undefined)
            }}
                  className={'iconfont  ' + props.iconName}/>
        </div>
    );
}

export default SysIcon;