import {handleUndefined} from "../../js/StringUtils";
import {useNavigate} from "react-router";

function BlogCard(props) {
    const blog = handleUndefined(props.blog, {});
    const index = handleUndefined(props.index, "");
    const className = handleUndefined(props.className, "");
    const navigate = useNavigate();
    return (
        <div className={"blog-list-item " + className} key={blog.title + index}
             onClick={() => {
                 navigate("/blog/detail/?url=" + blog.url + "&title=" + blog.title
                     + "&textImgColor=" + blog.textImgColor.replace('#', '') + "&textImgName=" + blog.textImgName, {});
             }}>
            <div className="blog-list-item-title-img"
                 style={{backgroundColor: blog.textImgColor}}>
                {blog.textImgName}
            </div>
            <div className="blog-list-item-text">
                <div className="blog-list-item-title">
                    <div className="left">{blog.title}</div>
                    <div className="right">{blog.timeStr}</div>
                </div>
                <div className="blog-list-item-subtitle">{blog.subTitle}</div>
            </div>
        </div>
    );
}

export default BlogCard;