import SysMain from "../../component/SysMain";
import React, {useEffect, useState} from "react";
import {getNextBlog} from "../../js/DataUtils";
import SysIcon from "../../component/SysIcon";
import SysMarkdown from "../../component/SysMarkdown";
import SysHeader from "../../component/SysHeader";
import {useNavigate} from "react-router";

function BlogCard() {
    const [nextBlog, setNextBlog] = useState(getNextBlog());
    const [nextBlogShow, setNextBlogShow] = useState(true);
    let [mdContent, setMdContent] = useState("");
    let navigate = useNavigate();
    const headerOption = {
        left: <SysIcon iconName="icon-home"/>,
        leftClick: () => {
            navigate("/");
        },
        headerAutoActive: false
    };
    useEffect(() => {
        fetch(nextBlog.url)
            .then((res) => res.text())
            .then((text) => setMdContent(text));
    });
    return (
        <div>
            <SysHeader option={headerOption}/>
            <SysMain>
                <div className="blog-card animate__animated animate__zoomIn"
                     style={{display: nextBlogShow ? "block" : "none"}}>
                    <div className="blog-card-head">
                        {nextBlog.title}
                    </div>
                    <div className="blog-card-content">
                        <SysMarkdown mdContent={mdContent}/>
                    </div>
                </div>
                <div className="blog-card-next-btn" onClick={() => {
                    setNextBlogShow(false);
                    setTimeout(function () {
                        setNextBlogShow(true);
                    }, 300);
                    setNextBlog(getNextBlog());
                }}>
                    <SysIcon iconName="icon-down"/>
                </div>
            </SysMain>
        </div>
    );
}

export default BlogCard;