import BlogUser from "../../component/Blog/BlogUser";
import BlogList from "../../component/Blog/BlogList";
import {getBlogUserInfo, getNextBlog, getSearchDataList} from "../../js/DataUtils";
import {useNavigate} from "react-router";
import React, {useState} from "react";
import {CrossRingTwo, Info} from "@icon-park/react";
import SysHeader from "../../component/SysHeader";
import SysPopup from "../../component/SysPopup";
import SysMain from "../../component/SysMain";
import BlogCard from "../../component/Blog/BlogCard";
import SysMenuBtn from "../../component/SysMenuBtn";
import {Toast} from "antd-mobile";
import SysIcon from "../../component/SysIcon";

function BlogIndex() {
    const navigate = useNavigate();
    let [menuShow, setMenuShow] = useState(false);
    let [searchShow, setSearchShow] = useState(false);
    let [searchData, setSearchData] = useState([]);
    let [searchKey, setSearchKey] = useState("");
    let blogUserInfo = getBlogUserInfo();
    const avatarUrl = blogUserInfo.avatarUrl;
    const avatarBgUrl = blogUserInfo.avatarBgUrl;
    const userName = blogUserInfo.userName;
    const headerOption = {
        left: <SysIcon color="white" iconName="icon-touzi"/>,
        leftClick: () => {
            let nextBlog = getNextBlog();
            navigate("/blog/card/?url=" + nextBlog.url + "&title=" + nextBlog.title + "&textImgColor="
                + nextBlog.textImgColor.replace('#', '') + "&textImgName=" + nextBlog.textImgName + "&nextFlag=ture", {});
        },
        mid: "",
        right: <SysIcon color="white" iconName="icon-search"/>,
        rightClick: () => {
            setSearchShow(true);
        },
        headerAutoActive: false
    };
    const moreContent =
        <div>
            <SysMenuBtn icon={<CrossRingTwo/>} onClick={() => {
                if (getNextBlog().title) {
                    navigate("/blog/question");
                } else {
                    Toast.show({
                        icon: <Info/>,
                        content: '没有数据',
                    });
                }
                setMenuShow(false);
            }}/>
        </div>;
    const searchContent =
        <div>
            <div className="search-head">
                <div className="search-input">
                    <input type="text" placeholder="请输入关键词" value={searchKey} onChange={(e) => {
                        setSearchData([]);
                        setSearchKey(e.target.value);
                    }}/>
                    <div className="search-btn" onClick={() => {
                        setSearchData(getSearchDataList(searchKey));
                    }}>
                        <SysIcon color="white" iconName="icon-search"/>
                    </div>
                </div>
            </div>
            <div className="search-content">
                {
                    searchData.map((blog, index) => {
                        return (
                            <BlogCard className="animate__animated animate__zoomIn" key={index} blog={blog}
                                      index={index}/>
                        );
                    })
                }
            </div>
        </div>
    ;
    return (
        <div>
            <SysHeader option={headerOption}></SysHeader>
            <SysMain>
                <BlogUser avatarUrl={avatarUrl} avatarBgUrl={avatarBgUrl} userName={userName}/>
                <BlogList/>
            </SysMain>
            <SysPopup show={menuShow} setShow={() => setMenuShow()} content={moreContent}/>
            <SysPopup show={searchShow} setShow={() => setSearchShow()} content={searchContent}/>
        </div>
    );
}

export default BlogIndex;