export function sortJson(jsonArray, sortKey) {
    for (let i = 0; i < jsonArray.length - 1; i++) {
        for (let j = 0; j < jsonArray.length - i - 1; j++) {
            let temp;
            if (jsonArray[j + 1][sortKey] > jsonArray[j][sortKey]) {
                temp = jsonArray[j];
                jsonArray[j] = jsonArray[j + 1];
                jsonArray[j + 1] = temp;
            }
        }
    }
    return jsonArray;
}